
@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700');
* {
  margin: 0;
  padding: 0;
}
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  font-family: 'Lato'
}
.container {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.container.left {
  background: linear-gradient(to right, #c02425, #f0cb35);
}
.container.right {
  background: linear-gradient(to right, #403a3e, #be5869);
}
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header {
  color: #eee;
  font-size: 1.5em;
  text-transform: uppercase;
  margin-bottom: 2em;
}
.time {
  font-size: 3em;
  color: #fff;
}
.date, .timezone {
  color: #eee;
}
.dropdown, .timezone {
  margin-top: 10px;
}
.dropdown {
  margin-top: 10px;
  color: #eee;
  border-bottom: 1px solid #eee;
  padding: 10px;
  cursor: pointer;
  position: relative;
  outline: none;
  min-width: 170px;
  max-width: 170px;
}
.selected {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  letter-spacing: 2px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
}
.text {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
.icon {
  font-size: 0.5em;
  color: #ddd;
  margin-left: 10px;
}
.search {
  top: -345px;
  position: absolute;
  width: 246px;
  left: -30px;
  background: none;
  border: none;
  outline: none;
  background: rgba(255,255,255,0.6);
  padding: 10px;
  box-sizing: border-box;
  color: #eee;
  border-top: 3px solid #eee;
  border-right: 3px solid #eee;
  border-left: 3px solid #eee;
}
.search::-webkit-input-placeholder {
  color: #fff;
}
.search:-ms-input-placeholder {
  color: #fff;
}
.search::placeholder {
  color: #fff;
}
.selection {
  width: 240px;
  height: 300px;
  overflow-y: scroll;
  background-color: rgba(255,255,255,0.6);
  border-right: 3px solid #eee;
  border-bottom: 3px solid #eee;
  border-left: 3px solid #eee;
  position: absolute;
  top: -310px;
  left: -30px;
}
.selection::-webkit-scrollbar {
  width: 7px;
}
.selection::-webkit-scrollbar-track {
  background: transparent;
}
.selection::-webkit-scrollbar-thumb {
  background-color: #ddd;
  border-radius: 10px;
}
.item {
  padding: 10px;
}
.item:hover {
  background-color: rgba(255,255,255,0.3)
}



